@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap);
*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	font-family: 'DM Sans', sans-serif;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

:root {
	--background: #0f0f11;
	--background-100: #1b1b1f;
	--background-200: #232327;
	--highlight: #59c794;
	--highlight-100: #42b47f;

	--wall: #1b1b1f;
	--goal: #bb3232;
	--start: #59c794;
	--path: #6d8ea3;
	--closed: #433455;
	--open: #31315a;
	--current: #29ffd1;
}

.app {
	width: 100vw;
	height: 100vh;
	color: white;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.app__layout {
	display: grid;
	grid-template-columns: -webkit-max-content auto;
	grid-template-columns: max-content auto;
	height: 100vh;
}

.app__layout > div {
	padding: 30px 20px;
}

.column__1 {
	background: var(--background);
}

.column__2 {
	background: var(--background);
}

.main__title {
	font-size: 1.7rem;
	margin-bottom: 25px;
	/* margin-left: 28px; */
	width: -webkit-max-content;
	width: max-content;
}

.main__controls {
	width: -webkit-max-content;
	width: max-content;
}

.small__screen {
	display: none;
}

@media screen and (max-width: 800px) {
	.app__layout {
		grid-template-columns: auto;
		grid-column-gap: -1px;
		-webkit-column-gap: -1px;
		        column-gap: -1px;
	}

	.column__1 {
		height: -webkit-max-content;
		height: max-content;
		display: -webkit-flex;
		display: flex;
		-webkit-justify-content: center;
		        justify-content: center;
	}

	.column__2 {
		height: 70vh;
		display: -webkit-flex;
		display: flex;
		-webkit-justify-content: center;
		        justify-content: center;
	}

	html {
		font-size: 14px;
	}

	.big__screen {
		display: none;
	}

	.small__screen {
		display: block;
	}
}

.grid__row {
	display: -webkit-flex;
	display: flex;
}

.grid__node {
	width: 25px;
	height: 25px;
	border-top: 1px solid var(--wall);
	border-left: 1px solid var(--wall);
}

.grid__row .grid__node:last-child {
	border-right: 1px solid var(--wall);
}

.grid__row:last-child .grid__node {
	border-bottom: 1px solid var(--wall);
}

.grid__row:first-child .grid__node:first-child {
	border-radius: 8px 0 0 0;
}

.grid__row:first-child .grid__node:last-child {
	border-radius: 0 8px 0 0;
}

.grid__row:last-child .grid__node:first-child {
	border-radius: 0 0 0 8px;
}

.grid__row:last-child .grid__node:last-child {
	border-radius: 0 0 8px 0;
}

.colour__wall {
	background: var(--wall);
}

.colour__open {
	background: var(--open);
}

.colour__closed {
	background: var(--closed);
}

.colour__path {
	background: var(--path);
}

.colour__start {
	background: var(--start);
}

.colour__goal {
	background: var(--goal);
}

.colour__current {
	background: var(--current);
}

.dropdown {
	margin: 10px 0;
	width: -webkit-max-content;
	width: max-content;
}

.title__container {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	font-size: 1.2rem;
	width: -webkit-max-content;
	width: max-content;
	cursor: pointer;
	transition: color 100ms;
}

.dropdown span {
	padding-top: 2px;
}

.title__container:hover {
	color: var(--start);
}

.title__container svg {
	margin-right: 5px;
}

.dropdown ul {
	list-style: none;
}

.dropdown li {
	font-size: 1.1em;
	margin-left: 40px;
	margin-top: 6px;
	width: -webkit-max-content;
	width: max-content;
	cursor: pointer;
	transition: color 100ms;
}

.dropdown li:hover {
	color: var(--start);
}

.button {
	font-size: 1.1em;
	transition: color 100ms;
	cursor: pointer;
	margin: 10px 0;
	margin-left: 29px;
}

.button:hover {
	color: var(--start);
}

.list__container {
	margin: 25px 0;
	margin-left: 29px;
}

.list__title {
	font-size: 16px;
	font-weight: 700;
}

.list__item {
	font-size: 16px;
	margin: 10px 0;
	position: relative;
	background: none !important;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.list__key {
	width: 18px;
	height: 18px;
	position: absolute;
	left: -24px;
	border-radius: 3px;
}

.list__row {
	display: -webkit-flex;
	display: flex;
}

.list__col:nth-child(2) {
	margin-left: 50px;
}

.colour__goal {
	background: var(--goal);
}

.colour__start {
	background: var(--start);
}

.colour__path {
	background: var(--path);
}

.colour__closed {
	background: var(--closed);
}

.colour__open {
	background: var(--open);
}

.colour__wall {
	background: var(--wall);
}

.radio__buttons {
	margin: 25px 0;
	margin-left: 29px;
}

.radio__buttons ul {
	display: -webkit-flex;
	display: flex;
	list-style: none;
	background: var(--background-100);
	padding: 5px;
	width: -webkit-max-content;
	width: max-content;
	border-radius: 4px;
}

.radio__buttons li {
	padding: 5px 10px;
	margin-right: 5px;
	border-radius: 4px;
	cursor: pointer;
	transition: background 100ms;
}

.radio__buttons li:hover {
	background: var(--background-200);
}

.radio__buttons li:last-child {
	margin-right: 0;
}

.active {
	color: var(--start);
	background: var(--background) !important;
}

@media screen and (max-width: 800px) {
	.radio__buttons {
		margin-left: 0;
		display: -webkit-flex;
		display: flex;
		-webkit-justify-content: center;
		        justify-content: center;
	}
}

.big__button {
	font-size: 1.1em;
	transition: color 100ms;
	cursor: pointer;
	margin: 10px 0;
	/* margin-left: 29px; */
	background: var(--start);
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	padding: 10px 0;
	border-radius: 4px;
	transition: background 100ms;
}

.big__button:hover {
	background: var(--highlight-100);
}

